import axios from "axios";
import MockAdapter from "axios-mock-adapter";

let acInterface = {
    getTablesDataUrl: "/JB4DCBuilder/Rest/Builder/DataStorage/DataBase/Table/GetTablesForZTreeNodeList",
    getTableFieldsUrl: "/JB4DCBuilder/Rest/Builder/DataStorage/DataBase/Table/GetTableFieldsByTableId",
    getTablesFieldsByTableIdsUrl: "/JB4DCBuilder/Rest/Builder/DataStorage/DataBase/Table/GetTablesFieldsByTableIds",
    getEnvVariableGroupTreeDataUrl: "/JB4DCBuilder/Rest/Builder/EnvVariableGroup/GetTreeData",
    getEnvVariableListDataUrl: "/JB4DCBuilder/Rest/Builder/EnvVariable/GetListData",
    getDataSetsForZTreeNodeListUrl: "/JB4DCBuilder/Rest/Builder/DataSet/DataSetMain/GetDataSetsForZTreeNodeList",
    getDataSetDataUrl: "/JB4DCBuilder/Rest/Builder/DataSet/DataSetMain/GetDataSetData",
    getWebFormForZTreeNodeListUrl: "/JB4DCBuilder/Rest/Builder/Form/GetWebFormForZTreeNodeList",
    getFormMainTableFieldsUrl: "/JB4DCBuilder/Rest/Builder/Form/GetFormMainTableFields",
    getAPISForZTreeNodeListUrl: "/JB4DCBuilder/Rest/Builder/ApiItem/GetAPISForZTreeNodeList",
    getDictionaryEntityGroupTreeDataUrl: "/JB4DCBuilder/Rest/SystemSetting/Dict/DictionaryGroup/GetTreeData",

    saveWebListDesignUrl: "/JB4DCBuilder/Rest/Builder/List/SaveEdit",
    getWebListDesignPOUrl: "/JB4DCBuilder/Rest/Builder/List/GetDetailData"
}

let storeDataSet = {};

let RemoteRestInterface = {
    getTablesData(sendData) {
        return axios.post(acInterface.getTablesDataUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getTableFieldsSync(sendData) {
        //axios,.db-table-relation-comp.vue
        let promiseObj = new promise((resolve, reject) => {
            AjaxUtility.PostSync(acInterface.getTableFieldsUrl, sendData, function (result) {
                let response = {
                    data: result
                }
                resolve(response)
            }, this);
        });
        return promiseObj;
        //successFunc(getTableFieldsUrlData)
    },
    getAllTablesFields(sendData) {
        return axios.post(acInterface.getTablesFieldsByTableIdsUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getEnvVariableGroupTreeData(sendData) {
        return axios.post(acInterface.getEnvVariableGroupTreeDataUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getEnvVariableListData(sendData) {
        return axios.post(acInterface.getEnvVariableListDataUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getDataSetsForZTreeNodeList(sendData) {
        return axios.post(acInterface.getDataSetsForZTreeNodeListUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getDataSetData(dataSetId) {
        let promiseObj;
        if (storeDataSet[dataSetId]) {
            let dataSetResponseData = storeDataSet[dataSetId];
            promiseObj = new Promise((resolve, reject) => {
                resolve({
                    data: dataSetResponseData
                })
            });
            //successFunc(dataSetResponseData);
            return promiseObj;
        }

        promiseObj = new Promise((resolve, reject) => {
            axios.post(acInterface.getDataSetDataUrl, {
                "op": "view",
                "recordId": dataSetId
            }).then(function (response) {
                //successFunc(response.data)
                storeDataSet[dataSetId] = response.data;
                resolve(response);
            }).catch(function (error) {
                console.log(error);
            });
        });
        return promiseObj;
    },
    getWebFormForZTreeNodeList(sendData) {
        return axios.post(acInterface.getWebFormForZTreeNodeListUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getFormMainTableFields(sendData) {
        return axios.post(acInterface.getFormMainTableFieldsUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getAPISForZTreeNodeList(sendData) {
        return axios.post(acInterface.getAPISForZTreeNodeListUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getDictionaryEntityGroupTreeData(sendData) {
        return axios.post(acInterface.getDictionaryEntityGroupTreeDataUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    saveWebListDesign(sendData) {
        console.log(sendData);
        return axios.post(acInterface.saveWebListDesignUrl, sendData).catch(function (error) {
            console.log(error);
        });
    },
    getWebListDesignPOAndBindTo(sendData) {
        let sendDataURLSearchParams = sendDataToURLSearchParams(sendData);
        return axios.post(acInterface.getWebListDesignPOUrl, sendDataURLSearchParams).catch(function (error) {
            console.log(error);
        });
    }
}

function sendDataToURLSearchParams(sendData) {
    const params = new URLSearchParams();
    for (let paramsKey in sendData) {
        params.append(paramsKey, sendData[paramsKey]);
    }
    return params;
}

